    toggleToast(id: string, change = null) {
        const changeArr = ["show", "hide"];
        const element: HTMLElement | null = document.getElementById(id);
        if (change !== null) {
            const changeIndex = changeArr.indexOf(change)
            const otherIndex = 1 - changeIndex;
            element?.classList.remove(changeArr[otherIndex])
            element?.classList.add(changeArr[changeIndex])
            return true;
        }
        if (element) {
            if (element.classList.contains("hide")) {
                element.classList.remove("hide")
                element.classList.add("show")
            } else {
                element.classList.remove("show")
                element.classList.add("hide")
            }
        }
        return true;
    }
